import { ctx, canvas } from '../../screens/canvas/js/index';

////part of code from https://codepen.io/retrofuturistic/pen/tlbHE///////////////////////

let dragSrcEl = null;

function handleDragStart(e) {
  ctx.globalCompositeOperation = "source-over";
  // Target (this) element is the source node.
  dragSrcEl = this;
  e.dataTransfer.effectAllowed = 'move';
  console.log(this.outerHTML)
  const currentCanvas = this.querySelector('.canvas2');
  localStorage.setItem('framecanvasName', currentCanvas.toDataURL());
  console.log(currentCanvas)
  e.dataTransfer.setData('text/html', this.outerHTML);

  this.classList.add('dragElem');
}

export function handleDragOver(e) {
  if (e.preventDefault) {
    e.preventDefault(); // Necessary. Allows us to drop.
  }
  this.classList.add('over');

  e.dataTransfer.dropEffect = 'move'; // See the section on the DataTransfer object.

  return false;
}

export function handleDragEnter(e) {
  console.log(e)
    // this / e.target is the current hover target.
}

function handleDragLeave(e) {
  console.log(e)
  this.classList.remove('over'); // this / e.target is previous target element.
}

function handleDrop(e) {
  // this/e.target is current target element.

  if (e.stopPropagation) {
    e.stopPropagation(); // Stops some browsers from redirecting.
  }

  // Don't do anything if dropping the same column we're dragging.
  if (dragSrcEl != this) {
    // Set the source column's HTML to the HTML of the column we dropped on.
    //alert(this.outerHTML);
    //dragSrcEl.innerHTML = this.innerHTML;
    //this.innerHTML = e.dataTransfer.getData('text/html');
    const parentNode = document.querySelector('.frame-containers');
    this.parentNode.removeChild(dragSrcEl);
    var dropHTML = e.dataTransfer.getData('text/html');
    let dropElem;
    if (this === parentNode.children[0]) {
      this.insertAdjacentHTML('beforebegin', dropHTML);
      dropElem = this.previousSibling;
    } else {
      this.insertAdjacentHTML('afterend', dropHTML);
      dropElem = this.nextSibling;
    }

    for (let elem of parentNode.children) {
      if (elem.classList.contains('active-frame')) {
        elem.classList.remove('active-frame');
      }
    }
    dropElem.classList.add('active-frame');
    const currentFrame = document.querySelector('.active-frame');
    const currentCanvas = currentFrame.querySelector('.canvas2');
    const currentCtx = currentCanvas.getContext('2d');
    const frameDataURL = localStorage.getItem('framecanvasName');
    const img = new Image;
    img.src = frameDataURL;
    img.onload = function() {
      currentCtx.drawImage(img, 0, 0);
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      ctx.imageSmoothingEnabled = false;
      ctx.drawImage(img, 0, 0, 640, 640);
    }
    for (let i = 0; i < parentNode.children.length; i++) {
      parentNode.children[i].querySelector('.frame-number').innerHTML = i + 1;
    }
    addDnDHandlers(dropElem);
  }
  this.classList.remove('over');
  return false;
}

export function handleDragEnd(e) {
  console.log(e)
    // this/e.target is the source node.
  this.classList.remove('over');

  /*[].forEach.call(cols, function (col) {
    col.classList.remove('over');
  });*/
}

export function addDnDHandlers(elem) {
  elem.addEventListener('dragstart', handleDragStart, false);
  elem.addEventListener('dragenter', handleDragEnter, false)
  elem.addEventListener('dragover', handleDragOver, false);
  elem.addEventListener('dragleave', handleDragLeave, false);
  elem.addEventListener('drop', handleDrop, false);
  elem.addEventListener('dragend', handleDragEnd, false);
}

