import { smallCanvas, ctxsmallCanvas, ctx } from '../canvas/js/index';

export function mouseOverprev(event) {
  let target = event.target.closest('.small-animation');
  if (!target) {
    return
  }
  let elementList = target.querySelectorAll('button');
  for (let elem of elementList) {
    elem.style.opacity = '1';
  }
}

export function mouseOutprev(event) {
  let target = event.target.closest('.small-animation');
  if (!target) {
    return
  }
  let elementList = target.querySelectorAll('button');
  for (let elem of elementList) {
    elem.style.opacity = '0';
  }
}

export function getFrameUrl() {
  const arrOfFrame = [];
  const parentNode = document.querySelector('.frame-containers');
  for (let i = 0; i < parentNode.children.length; i++) {
    const canvasElement = parentNode.children[i].querySelector('.canvas2');
    const frameDataURL = canvasElement.toDataURL();
    arrOfFrame.push(frameDataURL)
  }
  return arrOfFrame;
}

export function displaycurrentContext(elem) {
  const img = new Image;
  img.src = elem;
  img.onload = function() {
    ctxsmallCanvas.clearRect(0, 0, smallCanvas.width, smallCanvas.height)
    ctx.imageSmoothingEnabled = false;
    ctxsmallCanvas.drawImage(img, 0, 0, 200, 200);
  }
}

export function fullScreen(element) {
  if (element.requestFullscreen) {
    element.requestFullscreen();
  } else if (element.webkitrequestFullscreen) {
    element.webkitRequestFullscreen();
  } else if (element.mozRequestFullscreen) {
    element.mozRequestFullScreen();
  }
}