export function drawInitCanvas(w, h, canvas, ctx) {
  const { width } = canvas;
  const { height } = canvas;
  const xStep = width / w;
  const yStep = height / h;
  const sizeCanvas = document.querySelector('.size-canvas');
  sizeCanvas.textContent = `[${w}-${h}]`;
  for (let i = 0; i <= w; i++) {
    for (let j = 0; j <= h; j++) {
      ctx.fillStyle = 'rgb(0, 255, 0, 0)';
      ctx.fillRect(xStep * i, yStep * j, xStep, yStep);
    }
  }
}

export function helperSizeCanvas(a, b, canvas, ctx, drawInitCanvas) {
  drawInitCanvas(a, b, canvas, ctx);
  const elem = document.querySelector('.button-size');
  elem.hidden = true;
}