import { ctx, canvas, primarytCOlor, secondaryCOlor, canvasWidth, canvasHeight, createPreview } from '../index';
import { controlPixel, createArrOFWidthMatrix, createArrOFHeightMatrix } from './pen';

export const mouse = {
  x: 0,
  y: 0,
};



export function determineColor(event) {
  const arrOFWidthMatrix = createArrOFWidthMatrix(canvas.width, canvasWidth);
  const arrOFHeightMatrix = createArrOFHeightMatrix(canvas.height, canvasHeight);

  if (canvas.width == canvas.width / canvasWidth) {
    mouse.x = 0;
    mouse.y = 0;

  } else {
    mouse.x = event.pageX - this.offsetLeft;
    mouse.x = controlPixel(arrOFWidthMatrix, mouse.x);
    mouse.y = event.pageY - this.offsetTop;
    mouse.y = controlPixel(arrOFHeightMatrix, mouse.y);
  }


  let currentPixel = ctx.getImageData(mouse.x, mouse.y, 1, 1);
  let data = currentPixel.data;
  let rgba = 'rgba(' + data[0] + ', ' + data[1] +
    ', ' + data[2] + ', ' + (data[3] / 255) + ')';
  let x = mouse.x;
  let y = mouse.y;
  let newPixel;
  let newdata;
  let newrgba;
  for (let i = y; i <= canvas.height; i += canvas.height / canvasHeight) {
    newPixel = ctx.getImageData(x, i, 1, 1);
    newdata = newPixel.data;
    newrgba = 'rgba(' + newdata[0] + ', ' + newdata[1] +
      ', ' + newdata[2] + ', ' + (newdata[3] / 255) + ')';
    if (rgba === newrgba) {
      if (event.which == 1) {
        ctx.fillStyle = primarytCOlor;
        ctx.fillRect(x, i, canvas.width / canvasWidth, canvas.height / canvasHeight);
        for (let j = x + canvas.width / canvasWidth; j <= canvas.width; j += canvas.width / canvasWidth) {
          newPixel = ctx.getImageData(j, i, 1, 1);
          newdata = newPixel.data;
          newrgba = 'rgba(' + newdata[0] + ', ' + newdata[1] +
            ', ' + newdata[2] + ', ' + (newdata[3] / 255) + ')';
          if (rgba === newrgba) {
            if (event.which == 1) {
              ctx.fillStyle = primarytCOlor;
              ctx.fillRect(j, i, canvas.width / canvasWidth, canvas.height / canvasHeight);
              localStorage.setItem('canvasName', canvas.toDataURL());
              createPreview();
            } else if (event.which == 3) {
              ctx.fillStyle = secondaryCOlor;
              ctx.fillRect(j, i, canvas.width / canvasWidth, canvas.height / canvasHeight);
              localStorage.setItem('canvasName', canvas.toDataURL());
              createPreview();
            }
          } else {
            break
          }
        }

        for (let k = x - canvas.width / canvasWidth; k >= 0; k -= canvas.width / canvasWidth) {
          newPixel = ctx.getImageData(k, i, 1, 1);
          newdata = newPixel.data;
          newrgba = 'rgba(' + newdata[0] + ', ' + newdata[1] +
            ', ' + newdata[2] + ', ' + (newdata[3] / 255) + ')';
          if (rgba === newrgba) {
            if (event.which == 1) {
              ctx.fillStyle = primarytCOlor;
              ctx.fillRect(k, i, canvas.width / canvasWidth, canvas.height / canvasHeight);
              localStorage.setItem('canvasName', canvas.toDataURL());
              createPreview();
            } else if (event.which == 3) {
              ctx.fillStyle = secondaryCOlor;
              ctx.fillRect(k, i, canvas.width / canvasWidth, canvas.height / canvasHeight);
              localStorage.setItem('canvasName', canvas.toDataURL());
              createPreview();
            }
          } else {
            break
          }
        }
        localStorage.setItem('canvasName', canvas.toDataURL());
        createPreview();
      } else if (event.which == 3) {
        ctx.fillStyle = secondaryCOlor;
        ctx.fillRect(x, i, canvas.width / canvasWidth, canvas.height / canvasHeight);
        localStorage.setItem('canvasName', canvas.toDataURL());
        createPreview();
      }
    } else {
      break
    }
  }

  for (let i = y - canvas.height / canvasHeight; i >= 0; i -= canvas.height / canvasHeight) {
    newPixel = ctx.getImageData(x, i, 1, 1);
    newdata = newPixel.data;
    newrgba = 'rgba(' + newdata[0] + ', ' + newdata[1] +
      ', ' + newdata[2] + ', ' + (newdata[3] / 255) + ')';

    if (rgba === newrgba) {
      if (event.which == 1) {
        ctx.fillStyle = primarytCOlor;
        ctx.fillRect(x, i, canvas.width / canvasWidth, canvas.height / canvasHeight);
        localStorage.setItem('canvasName', canvas.toDataURL());
        createPreview();
        for (let j = x + canvas.width / canvasWidth; j <= canvas.width; j += canvas.width / canvasWidth) {
          newPixel = ctx.getImageData(j, i, 1, 1);
          newdata = newPixel.data;
          newrgba = 'rgba(' + newdata[0] + ', ' + newdata[1] +
            ', ' + newdata[2] + ', ' + (newdata[3] / 255) + ')';
          if (rgba === newrgba) {
            if (event.which == 1) {
              ctx.fillStyle = primarytCOlor;
              ctx.fillRect(j, i, canvas.width / canvasWidth, canvas.height / canvasHeight);
              localStorage.setItem('canvasName', canvas.toDataURL());
              createPreview();
            } else if (event.which == 3) {
              ctx.fillStyle = secondaryCOlor;
              ctx.fillRect(j, i, canvas.width / canvasWidth, canvas.height / canvasHeight);
              localStorage.setItem('canvasName', canvas.toDataURL());
              createPreview();
            }
          } else {
            break
          }
        }

        for (let k = x - canvas.width / canvasWidth; k >= 0; k -= canvas.width / canvasWidth) {
          newPixel = ctx.getImageData(k, i, 1, 1);
          newdata = newPixel.data;
          newrgba = 'rgba(' + newdata[0] + ', ' + newdata[1] +
            ', ' + newdata[2] + ', ' + (newdata[3] / 255) + ')';
          if (rgba === newrgba) {
            if (event.which == 1) {
              ctx.fillStyle = primarytCOlor;
              ctx.fillRect(k, i, canvas.width / canvasWidth, canvas.height / canvasHeight);
              localStorage.setItem('canvasName', canvas.toDataURL());
              createPreview();
            } else if (event.which == 3) {
              ctx.fillStyle = secondaryCOlor;
              ctx.fillRect(k, i, canvas.width / canvasWidth, canvas.height / canvasHeight);
              localStorage.setItem('canvasName', canvas.toDataURL());
              createPreview();
            }
          } else {
            break
          }
        }
        localStorage.setItem('canvasName', canvas.toDataURL());
        createPreview();
      } else if (event.which == 3) {
        ctx.fillStyle = secondaryCOlor;
        ctx.fillRect(x, i, canvas.width / canvasWidth, canvas.height / canvasHeight);
        localStorage.setItem('canvasName', canvas.toDataURL());
        createPreview();
      }
    } else {
      break
    }
  }

  for (let i = x + canvas.width / canvasWidth; i <= canvas.width; i += canvas.width / canvasWidth) {
    newPixel = ctx.getImageData(i, y, 1, 1);
    newdata = newPixel.data;
    newrgba = 'rgba(' + newdata[0] + ', ' + newdata[1] +
      ', ' + newdata[2] + ', ' + (newdata[3] / 255) + ')';
    if (rgba === newrgba) {
      if (event.which == 1) {
        ctx.fillStyle = primarytCOlor;
        ctx.fillRect(i, y, canvas.width / canvasWidth, canvas.height / canvasHeight);
        localStorage.setItem('canvasName', canvas.toDataURL());
        createPreview();
      } else if (event.which == 3) {
        ctx.fillStyle = secondaryCOlor;
        ctx.fillRect(i, y, canvas.width / canvasWidth, canvas.height / canvasHeight);
        localStorage.setItem('canvasName', canvas.toDataURL());
        createPreview();
      }
    } else {
      break
    }
  }

  for (let i = x - canvas.width / canvasWidth; i >= 0; i -= canvas.width / canvasWidth) {
    newPixel = ctx.getImageData(i, y, 1, 1);
    newdata = newPixel.data;
    newrgba = 'rgba(' + newdata[0] + ', ' + newdata[1] +
      ', ' + newdata[2] + ', ' + (newdata[3] / 255) + ')';
    if (rgba === newrgba) {
      if (event.which == 1) {
        ctx.fillStyle = primarytCOlor;
        ctx.fillRect(i, y, canvas.width / canvasWidth, canvas.height / canvasHeight);
        localStorage.setItem('canvasName', canvas.toDataURL());
        createPreview();
      } else if (event.which == 3) {
        ctx.fillStyle = secondaryCOlor;
        ctx.fillRect(i, y, canvas.width / canvasWidth, canvas.height / canvasHeight);
        localStorage.setItem('canvasName', canvas.toDataURL());
        createPreview();
      }
    } else {
      break
    }
  }
}