import { bucket, primary, secondary, swap, pen, eraser, stroke, bucketAll, save, resize, picker, keyboad } from '../index';


export function getKeydown() {
  document.addEventListener('keydown', (event) => {
    switch (event.code) {
      case 'KeyB':
        bucket.click();
        break;
      case 'KeyD':
        primary.click();
        break;
      case 'KeyF':
        secondary.click();
        break;
      case 'KeyG':
        swap.click();
        break;
      case 'KeyP':
        pen.click();
        break;
      case 'KeyO':
        picker.click();
        break;
      case 'KeyE':
        eraser.click();
        break;
      case 'KeyT':
        stroke.click();
        break;
      case 'KeyA':
        bucketAll.click();
        break;
      case 'KeyS':
        save.click();
        break;
      case 'KeyR':
        resize.click();
        break;
      case 'KeyK':
        keyboad.click();
        break;
      default:
        break;
    }
  })
}

export function changeShortcuts(event) {
  let target = event.target.closest('.next-shortcuts');
  if (!target) {
    return;
  }
  const value = target.value.toUpperCase();
  const element = target.closest('div');
  element.querySelector('.current-shortcuts').textContent = target.value;
  const className = element.classList.value.split(' ')[1];
  const cgangeElem = document.querySelector(`#${className}`);
  document.addEventListener('keydown', (event) => {
    switch (event.code) {
      case `Key${value}`:
        cgangeElem.click();
        break;
      default:
        break;
    }
  });
}

export function findLessN(findLessElement, arrOfSizes) {
  let lessElem = findLessElement(arrOfSizes);
  let n;
  switch (lessElem) {
    case arrOfSizes[0]:
      n = 1;
      break;
    case arrOfSizes[1]:
      n = 2;
      break;
    case arrOfSizes[2]:
      n = 3;
      break;
    case arrOfSizes[3]:
      n = 4;
      break;
    default:
      break;
  }
  return n;
}

export function findLessElement(arrOfSizes) {
  const row = document.querySelector('.row-0');
  const elem = row.querySelector('.active-button');
  const index = arrOfSizes.indexOf(elem);
  let lessElem;
  if (index === 0) {
    lessElem = arrOfSizes[index];
  } else {
    lessElem = arrOfSizes[index - 1];
  }
  return lessElem;
}

export function findMaxElement(arrOfSizes) {
  const row = document.querySelector('.row-0');
  const elem = row.querySelector('.active-button');
  const index = arrOfSizes.indexOf(elem);
  let MaxElem;
  if (index === (arrOfSizes.length - 1)) {
    MaxElem = arrOfSizes[index];
  } else {
    MaxElem = arrOfSizes[index + 1];
  }
  return MaxElem;
}

export function findMaxN(findMaxElement, arrOfSizes) {
  let MaxElem = findMaxElement(arrOfSizes);
  let n;
  switch (MaxElem) {
    case arrOfSizes[0]:
      n = 1;
      break;
    case arrOfSizes[1]:
      n = 2;
      break;
    case arrOfSizes[2]:
      n = 3;
      break;
    case arrOfSizes[3]:
      n = 4;
      break;
    default:
      break;
  }
  return n;
}