export function changeOrderColor(primarytCOlor, secondaryCOlor) {
  const intermediateValue = primarytCOlor;
  primarytCOlor = secondaryCOlor;
  document.querySelector('.span-primary').style.background = primarytCOlor;
  secondaryCOlor = intermediateValue;
  document.querySelector('.span-secondary').style.background = secondaryCOlor;
}