import { ctx, canvas, primarytCOlor, secondaryCOlor, bErasing, canvasWidth, canvasHeight, createPreview } from '../index';


export function createArrOFWidthMatrix(width, widthMatrix) {
  const arrOFWidthMatrix = [];
  for (let i = width / widthMatrix; i <= width - width / widthMatrix; i += width / widthMatrix) {
    arrOFWidthMatrix.push(i);
  }
  return arrOFWidthMatrix;
}

export function createArrOFHeightMatrix(height, heightMatrix) {
  const arrOFHeightMatrix = [];
  for (let i = height / heightMatrix; i <= height - height / heightMatrix; i += height / heightMatrix) {
    arrOFHeightMatrix.push(i);
  }
  return arrOFHeightMatrix;
}

export function controlPixel(arr, x) {
  for (let i = 0; i < arr.length; i++) {
    if (x < arr[0]) {
      x = 0;
      break
    }
    if (x >= arr[i] && x < arr[i + 1]) {
      x = arr[i];
    }
    if (x >= arr[arr.length - 1]) {
      x = arr[arr.length - 1];
      break
    }
  }
  return x
}

export let draw = false;
export const mouse = {
  x: 0,
  y: 0,
};

export function startDraw(event) {

  let arrOFWidthMatrix = createArrOFWidthMatrix(canvas.width, canvasWidth);
  let arrOFHeightMatrix = createArrOFHeightMatrix(canvas.height, canvasHeight);

  if (canvas.width == canvas.width / canvasWidth) {
    mouse.x = 0;
    mouse.y = 0;

  } else {
    mouse.x = event.pageX - this.offsetLeft;
    mouse.x = controlPixel(arrOFWidthMatrix, mouse.x);

    mouse.y = event.pageY - this.offsetTop;
    mouse.y = controlPixel(arrOFHeightMatrix, mouse.y);
  }

  draw = true;
  if (bErasing == true) {
    ctx.globalCompositeOperation = "destination-out";
  } else {
    ctx.globalCompositeOperation = "source-over";
  }
  if (event.which == 1) {
    ctx.beginPath();
    ctx.fillStyle = primarytCOlor;
    ctx.fillRect(mouse.x, mouse.y, canvas.width / canvasWidth, canvas.height / canvasHeight);
    localStorage.setItem('canvasName', canvas.toDataURL());
    createPreview();
  } else if (event.which == 3) {
    ctx.beginPath();
    ctx.fillStyle = secondaryCOlor;
    ctx.fillRect(mouse.x, mouse.y, canvas.width / canvasWidth, canvas.height / canvasHeight);
    localStorage.setItem('canvasName', canvas.toDataURL());
    createPreview();
  }
}

export function drawCanvas(event) {
  event.preventDefault();
  let arrOFWidthMatrix = createArrOFWidthMatrix(canvas.width, canvasWidth);
  let arrOFHeightMatrix = createArrOFHeightMatrix(canvas.height, canvasHeight);
  if (bErasing == true) {
    ctx.globalCompositeOperation = "destination-out";
  } else {
    ctx.globalCompositeOperation = "source-over";
  }
  if (draw === true) {
    if (canvas.width == canvas.width / canvasWidth) {
      mouse.x = 0;
      mouse.y = 0
    } else {
      mouse.x = event.pageX - this.offsetLeft;
      mouse.x = controlPixel(arrOFWidthMatrix, mouse.x);

      mouse.y = event.pageY - this.offsetTop;
      mouse.y = controlPixel(arrOFHeightMatrix, mouse.y);
    }
    if (event.which == 1) {
      ctx.fillStyle = primarytCOlor;
      ctx.fillRect(mouse.x, mouse.y, canvas.width / canvasWidth, canvas.height / canvasHeight);
      localStorage.setItem('canvasName', canvas.toDataURL());
      createPreview();
    } else if (event.which == 3) {
      ctx.fillStyle = secondaryCOlor;
      ctx.fillRect(mouse.x, mouse.y, canvas.width / canvasWidth, canvas.height / canvasHeight);
      localStorage.setItem('canvasName', canvas.toDataURL());
      createPreview();
    }
  }
}

export function stopDrow() {
  draw = false;
}