import { ctx, canvas } from '../../screens/canvas/js/index';

export function mouseOver(event) {
  let target = event.target.closest('.frame-container');
  if (!target) {
    return
  }
  let elementList = target.querySelectorAll('button');
  for (let elem of elementList) {
    elem.style.opacity = '1';
  }
}

export function mouseOut(event) {
  let target = event.target.closest('.frame-container');
  if (!target) {
    return
  }
  let elementList = target.querySelectorAll('button');
  for (let elem of elementList) {
    elem.style.opacity = '0';
  }
}

export function getActive(event) {
  let target = event.target.closest('.frame-container');
  let parentNode = document.querySelector('.frame-containers');
  for (let elem of parentNode.children) {
    if (elem.classList.contains('active-frame')) {
      elem.classList.remove('active-frame');
    }
  }
  ctx.globalCompositeOperation = "source-over";
  target.classList.add('active-frame');
  const currentCanvas = target.querySelector('.canvas2');
  const frameDataURL = currentCanvas.toDataURL();
  const img = new Image;
  img.src = frameDataURL;
  img.onload = function() {
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.imageSmoothingEnabled = false;
    ctx.drawImage(img, 0, 0, 640, 640);
  }
}