import { ctx, canvas, primarytCOlor, secondaryCOlor, canvasWidth, canvasHeight, createPreview } from '../index';
import { createArrOFWidthMatrix, createArrOFHeightMatrix, controlPixel } from './pen';


export let draw = false;
export const mouse = {
  x: 0,
  y: 0,
};

let beginX;
let beginY;
let finishX;
let finishY;

export function startDrawStroke(event) {

  let arrOFWidthMatrix = createArrOFWidthMatrix(canvas.width, canvasWidth);
  let arrOFHeightMatrix = createArrOFHeightMatrix(canvas.height, canvasHeight);

  if (canvas.width == canvas.width / canvasWidth) {
    mouse.x = 0;
    mouse.y = 0;
    beginX = mouse.x;
    beginY = mouse.y;
  } else {
    mouse.x = event.pageX - this.offsetLeft;
    mouse.x = controlPixel(arrOFWidthMatrix, mouse.x);
    mouse.y = event.pageY - this.offsetTop;
    mouse.y = controlPixel(arrOFHeightMatrix, mouse.y);
    beginX = mouse.x;
    beginY = mouse.y;
  }

  draw = true;
}

export function drawCanvasStroke(event) {
  event.preventDefault();
  let arrOFWidthMatrix = createArrOFWidthMatrix(canvas.width, canvasWidth);
  let arrOFHeightMatrix = createArrOFHeightMatrix(canvas.height, canvasHeight);

  if (draw === true) {
    if (canvas.width == canvas.width / canvasWidth) {
      mouse.x = 0;
      mouse.y = 0
      finishX = mouse.x;
      finishY = mouse.y;
    } else {
      mouse.x = event.pageX - this.offsetLeft;
      mouse.x = controlPixel(arrOFWidthMatrix, mouse.x);

      mouse.y = event.pageY - this.offsetTop;
      mouse.y = controlPixel(arrOFHeightMatrix, mouse.y);
      finishX = mouse.x;
      finishY = mouse.y;
    }
  }
}

export function stopDrowStroke(event) {
  const firstX = Math.min(beginX, finishX);
  const secondX = Math.max(beginX, finishX);
  const firstY = Math.min(beginY, finishY);
  const secondY = Math.max(beginY, finishY);

  if (event.which == 1) {
    let i, j;
    const c = Math.max(secondX, secondY);
    for (i = firstX, j = firstY; i <= c, j <= c; i += canvas.width / canvasWidth, j += canvas.height / canvasHeight) {
      ctx.beginPath();
      let a = i;
      let b = j;
      ctx.fillStyle = primarytCOlor;
      if (i > secondX) {
        a = secondX;
      }
      if (j > secondY) {
        b = secondY;
      }
      ctx.fillRect(a, b, canvas.width / canvasWidth, canvas.height / canvasHeight);
      localStorage.setItem('canvasName', canvas.toDataURL());
      createPreview();
    }
  } else if (event.which == 3) {
    let i, j;
    for (i = beginX, j = beginY; i <= finishX, j <= finishY; i += canvas.width / canvasWidth, j += canvas.height / canvasHeight) {
      ctx.beginPath();
      ctx.fillStyle = secondaryCOlor;
      ctx.fillRect(i, j, canvas.width / canvasWidth, canvas.height / canvasHeight);
      localStorage.setItem('canvasName', canvas.toDataURL());
      createPreview();
    }
  }

  draw = false;
}