import { ctx, canvas, primarytCOlor, secondaryCOlor, createPreview } from '../index';

export function changeColorOverCanvas(event) {
  if (event.which == 1) {
    ctx.fillStyle = primarytCOlor;
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    localStorage.setItem('canvasName', canvas.toDataURL());
    createPreview()
  } else if (event.which == 3) {
    ctx.fillStyle = secondaryCOlor;
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    localStorage.setItem('canvasName', canvas.toDataURL());
    createPreview()
  }
}