///////////////////ColorPicker tool///////////////////////////////////////////////////
picker.addEventListener('click', function() {
  canvas.removeEventListener('mousedown', startDraw);
  canvas.removeEventListener('mousemove', drawCanvas);
  canvas.removeEventListener('mouseup', stopDrow);
  canvas.removeEventListener('mousedown', startDrawStroke);
  canvas.removeEventListener('mousemove', drawCanvasStroke);
  canvas.removeEventListener('mouseup', stopDrowStroke);
  canvas.removeEventListener('click', changeColorOverCanvas);
  canvas.removeEventListener('click', determineColor);
  canvas.oncontextmenu = function() {
    ctx.globalCompositeOperation = "source-over";
    let x = event.pageX - this.offsetLeft;
    let y = event.pageY - this.offsetTop;
    let currentPixel = ctx.getImageData(x, y, 1, 1);
    let data = currentPixel.data;
    let rgba = 'rgba(' + data[0] + ', ' + data[1] +
      ', ' + data[2] + ', ' + (data[3] / 255) + ')';
    secondaryCOlor = rgba;
    document.querySelector('.span-secondary').style.background = secondaryCOlor;
    return false;
  }
  canvas.addEventListener('click', getColorPicker);
  active(picker, arrOfAction);
})

function getColorPicker(event) {
  ctx.globalCompositeOperation = "source-over";
  let x = event.pageX - this.offsetLeft;
  let y = event.pageY - this.offsetTop;
  let currentPixel = ctx.getImageData(x, y, 1, 1);
  let data = currentPixel.data;
  let rgba = 'rgba(' + data[0] + ', ' + data[1] +
    ', ' + data[2] + ', ' + (data[3] / 255) + ')';
  primarytCOlor = rgba;
  document.querySelector('.span-primary').style.background = primarytCOlor;
}


///////////////////work with frame///////////////////////////////////////////////////
buttonAddNewFrame.addEventListener('click', addFrame);

export function addFrame() {
  let parentNode = document.querySelector('.frame-containers');
  for (let elem of parentNode.children) {
    if (elem.classList.contains('active-frame')) {
      elem.classList.remove('active-frame');
    }
  }
  let li = document.querySelector('.frame-container').cloneNode(true);
  lastframeNumber++;
  li.querySelector('.frame-number').innerHTML = lastframeNumber;
  li.classList.add('active-frame');
  document.querySelector('.frame-containers').append(li);
  ctx.clearRect(0, 0, canvas.width, canvas.height);
  addDnDHandlers(li)
}

document.querySelector('.frames').addEventListener('mouseover', mouseOver);
document.querySelector('.frames').addEventListener('mouseout', mouseOut);

document.querySelector('main').addEventListener('click', function() {
  removeElement(event);
  dublicate(event);
  lastframeNumber = document.querySelector('.frame-containers').children.length;
});

document.querySelector('.frame-containers').addEventListener('click', getActive);

function dublicate(event) {
  let target = event.target.closest('.canvas-container-duplicate');
  if (!target) {
    return
  }
  ctx.globalCompositeOperation = "source-over";
  const element = target.closest('.frame-container');
  const framecanvas = element.querySelector('.canvas2');
  const frameDataURL = framecanvas.toDataURL();
  const li = element.cloneNode(true);
  const liCanvas = li.querySelector('.canvas2');
  const lictx = liCanvas.getContext('2d');
  const img = new Image;
  img.src = frameDataURL;
  img.onload = function() {
    lictx.drawImage(img, 0, 0);
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.imageSmoothingEnabled = false;
    ctx.drawImage(img, 0, 0, 640, 640);
  }
  element.after(li);
  lastframeNumber++;
  let parentNode = document.querySelector('.frame-containers');
  for (let elem of parentNode.children) {
    if (elem.classList.contains('active-frame')) {
      elem.classList.remove('active-frame');
    }
  }
  li.classList.add('active-frame');
  for (let i = 0; i < parentNode.children.length; i++) {
    parentNode.children[i].querySelector('.frame-number').innerHTML = i + 1;
  }
  addDnDHandlers(li);
  if (timerid) {
    clearTimeout(timerid);
  }
  timerid = startAnimate(currentSpeed);
}


export function createPreview() {
  const currentFrame = document.querySelector('.active-frame');
  const currentCanvas = currentFrame.querySelector('.canvas2');
  const currentCtx = currentCanvas.getContext('2d');
  currentCtx.clearRect(0, 0, currentCanvas.width, currentCanvas.height);
  const dataURL = localStorage.getItem('canvasName');
  const img = new Image;
  img.src = dataURL;
  img.onload = function() {
    currentCtx.imageSmoothingEnabled = false;
    currentCtx.drawImage(img, 0, 0, currentCanvas.width, currentCanvas.height);
  }
}
