//////////////////////////animation////////////////
let count = 0;

export function startAnimate(n) {
  let timerid = setInterval(() => {
    const arrOfFrame = getFrameUrl();
    let frame = arrOfFrame[count % arrOfFrame.length];
    if (arrOfFrame.length === 0) {
      frame = arrOfFrame[0];
    }
    displaycurrentContext(frame);
    count++
  }, 1000 / n);
  if (n === 0) {
    clearTimeout(timerid);
  }
  return timerid;
}

document.querySelector('#cowbell').addEventListener('input', function() {
  currentSpeed = Number(document.querySelector('#cowbell').value);
  document.querySelector('.speed-animation label').textContent = `${currentSpeed} FPS`
  if (timerid) {
    clearTimeout(timerid);
  }
  timerid = startAnimate(currentSpeed);
})

document.querySelector('.additional-tools').addEventListener('mouseover', mouseOverprev);
document.querySelector('.additional-tools').addEventListener('mouseout', mouseOutprev);
document.querySelector('#full-screen-mode').addEventListener('click', function() {
  fullScreen(smallCanvas)
})


getKeydown();
document.addEventListener('keydown', function(event) {
  if (event.code == 'KeyV' && (event.ctrlKey || event.metaKey)) {
    addFrame();
  } else if (event.code == 'KeyC' && (event.ctrlKey || event.metaKey)) {
    dublicateActiveFarm();
    lastframeNumber = document.querySelector('.frame-containers').children.length;
  } else if (event.code == 'KeyX' && (event.ctrlKey || event.metaKey)) {
    removeActiveFrame();
    lastframeNumber = document.querySelector('.frame-containers').children.length;
  } else if (event.code == 'BracketLeft') {
    let lessElem = findLessElement(arrOfSizes);
    let n = findLessN(findLessElement, arrOfSizes);
    canvasWidth = initionalCanvasWidth / n;
    canvasHeight = initionalCanvasHeight / n;
    handlerEventforPen();
    active(lessElem, arrOfSizes);
    active(pen, arrOfAction);
  } else if (event.code == 'BracketRight') {
    let MaxElem = findMaxElement(arrOfSizes);
    let n = findMaxN(findMaxElement, arrOfSizes);
    canvasWidth = initionalCanvasWidth / n;
    canvasHeight = initionalCanvasHeight / n;
    handlerEventforPen();
    active(MaxElem, arrOfSizes);
    active(pen, arrOfAction);
  }
})

